import { mapState,mapGetters } from 'vuex';
import wHead from './workbenchHead.vue'
import wArtisan from './workbenchArtisan.vue'
const chooseLocation = requirePlugin('chooseLocation');
export default {
	data() {
		return {
			location:null,
		}
	},
	onShow () {
		this.location = chooseLocation.getLocation();
		console.log('show,loc',this.location)// 如果点击确认选点按钮，则返回选点结果对象，否则返回null
	},
	onLoad() {
		console.log('onLoad')
	},
	components:{
		wHead,
		wArtisan
	},
	computed: {
		...mapState(['is_login']),
		...mapGetters(['user_role']),
	},
	methods: {
		init(){
			this.login_tips()
		},
		login_tips(){
			uni.showModal({
			    title: '提示',
			    content: '您还未登录，登录体验更佳',
				cancelText:'先看看',
				confirmText:'前往登录',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
						uni.navigateTo({
							url: '/pages/welcome/welcome'
						});
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
	}
}
